import React, { Component } from 'react'
import './menu.scss'
import { connect } from 'react-redux'
import { Table, Divider, Tag } from 'antd';
import Http from '../../services/http'


function getHtml(text){
    text = text.replace('\.\/images','https://shuatiapp.cn/images')
    return {
        __html:text
    }
}
class Menu extends Component {
    api = {
        getErrorQuestion: "/api/getErrorQuestion",
        setErrorToTrue:"/api/setErrorToTrue",
    }

    async componentDidMount() {
        let { getErrorQuestion } = this.api
        let { dispatch } = this.props
        const response = await Http.get(getErrorQuestion, {}) || {}
        let isSuccess = response && response.type
        if (!isSuccess) {
            throw new Error('拉取列表失败，请刷新！')
        }
        dispatch({
            type: "setErrList",
            payload: response.data.map(res => ({
                ...res,
                key:res.id
            }))
        })
    }

    checkQuestion = uid => {
        let { history } = this.props
        history.push(`wrongDetail/${uid}`)
    }

    fixed = async ({id}) => {
        let {setErrorToTrue} = this.api
        let {type} = await Http.get(setErrorToTrue,{
            id
        })
        type &&  await this.componentDidMount()
    }

    columns = [
        {
            title: 'question',
            dataIndex: 'question',
            key: 'question',
            width:"15%",
            render:text => <div dangerouslySetInnerHTML={getHtml(text)}></div>
        },
        {
            title: '答案',
            dataIndex: 'answer',
            key: 'answer',
            width:"5%"
        },
        {
            title: 'analysis',
            dataIndex: 'analysis',
            key: 'analysis',
            render:text => <div dangerouslySetInnerHTML={getHtml(text)}></div>,
            width:"15%"
        },
        {
            title: 'A',
            key: 'option_A',
            render:text => <div dangerouslySetInnerHTML={getHtml(text)}></div>,
            dataIndex: 'option_A'
        },
        {
            title: 'B',
            key: 'option_B',
            render:text => <div dangerouslySetInnerHTML={getHtml(text)}></div>,
            dataIndex: 'option_B'
        },
        {
            title: 'C',
            key: 'option_C',
            render:text => <div dangerouslySetInnerHTML={getHtml(text)}></div>,
            dataIndex: 'option_C'
        },
        {
            title: 'D',
            key: 'option_D',
            render:text => <div dangerouslySetInnerHTML={getHtml(text)}></div>,
            dataIndex: 'option_D'
        },
        {
            title: '提交人',
            key: 'user_id_array',
            dataIndex: 'user_id_array',
            width:"10%"
        },
        {
            title: '操作',
            key: 'action',
            width:"15%",
            render: (text, record) => (
                <span>
                    <a onClick={() => this.checkQuestion(record.id)}>修改 </a>
                    <Divider type="vertical" />
                    <a onClick={() => this.fixed(record)}>标记完成</a>
                </span>
            ),
        },
        
    ];

    render() {
        let { errorQuestionList } = this.props
        return (
            <div className="wrong-list">
                <h3>错题列表</h3>
                <Table columns={this.columns} dataSource={errorQuestionList} />
            </div>
        )
    }
}
const mapStateToProps = (state) => {
    return state.reducer || {}
}
export default connect(mapStateToProps)(Menu);


